<template>
  <div class="basicNeedForm">
    <div class="page-content">
    	<el-form ref="form" :model="form" label-width="80px" :rules="rules">
        <el-form-item label="" prop="name" >
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-select v-model="form.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker type="date" placeholder="" v-model="form.date1"  style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-time-picker type="fixed-time" placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="delivery" >
          <el-switch on-text="" off-text v-model="form.delivery"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="type" >
          <el-checkbox-group v-model="form.type">
            <el-checkbox label="/" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="resource" >
          <el-radio-group v-model="form.resource">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="desc" >
          <el-input type="textarea" v-model="form.desc"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('form')"></el-button>
          <el-button></el-button>
          <el-button @click="resetForm('form')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    /*  */
    let validateSpace = (rule, value, callback) => {
      if (value.trim().length == 0) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { validator: validateSpace, trigger: "blur, change" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ],
        region: [{ required: true, message: "", trigger: "change" }],
        date1: [
          { type: "date", required: true, message: "", trigger: "change" }
        ],
        date2: [
          { type: "date", required: true, message: "", trigger: "change" }
        ],
        type: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        resource: [{ required: true, message: "", trigger: "change" }],
        desc: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    onSubmit(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          alert("");
        } else {
          alert("");
        }
      });
    },
    resetForm(form) {
      this.$refs[form].resetFields();
    }
  }
};
</script>


